@import "./styles/examples.scss";

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

p {
    padding: 10px 0 10px 0;
}

h1 {
    font-size: 30px;
}

#main {
    color: $color;
}

body {
    margin: 0;
    background-color: "#747F7F";
    font-family: bradley hand;
}

header {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:bisque;
}

h1 {
    margin: 0;
}

h1:hover {
    cursor: default;
}

.right {
    display: flex;
    justify-content: space-between;
    width: 250px;
}

.audio {
    width: 250px;
    position: relative;
}

#sound {
    position: relative;
    left: 30px;
}

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
    background-color:bisque;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 50%;
    position: relative;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

#capy-pic {
    position: absolute;
    right: 30px;
    bottom: 30px;
    width: 100px;
    height: 100px;
}

.close:hover {
    cursor: pointer;
}

#myBtn {
    width: 100px;
    height: 40px;
    background-color:lightcyan;
    font-family: bradley hand;
    font-size: 15px;
}

#myBtn:hover {
    cursor: pointer;
}

#myBtn:focus {
    background-color:aquamarine;
}

.logos {
    height:40px;
    width: 100px;
}

.logos a {
    width: 40px;
    height: 40px;
    cursor: default;
}

.logos img:hover {
    cursor: pointer;
}

.modal-content p {
    font-family:Arial, Helvetica, sans-serif;
    // font-family:bradley hand;
}

.audio img:hover {
    cursor: pointer;
}

#arrow-keys {
    max-width: 200px;
    max-height: 200px;
}

#movement-text {
    font-weight: 600;
}